	module.exports = {"data":{"markdownRemark":{"html":"<p>Here's a link to a nice comparison of Google Scholar and Scrius. It points out that Google Scholar has become neglected and is no longer updated regularly. This is a super-unfortunate development; Google is the web's best hope for easy, inexpensive archiving of scholarly research. (In other news, however, Amazon is now offering scholarly articles for a fee. It's easy — they're delivered electronically, but they aren't cheap — about $5.00 per article.)</p>\n<p>Here's the link to the Google article:\n<a href=\"http://www.infotoday.com/online/jul05/OnTheNet.shtml\">Scholarly Web Searching: Google Scholar and Scirus</a></p>","frontmatter":{"created_at":"August 03, 2005","path":"/2005/08/03/online-resources-for-evaluation-nonprofit-programs","title":"Online Resources for Evaluation Nonprofit Programs","tags":["google","scholarship","research"]}}},"pathContext":{}}

